
.photos{
   background-color: #282c34; ;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 2rem;
    row-gap: 0.5rem;
    column-gap: 0.5rem;
}

.photo{
    width: 300px;
    height: 300px;
}

.photo img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}